const Eventos=(props)=>{
  const { name,   description } = props.evento;
 
  return (
    <div className="col-md-12 col-12 mb-4">
      <div className="card"> 
        <div className="card-body">
        <h3 className="card-title">{name}</h3>
          <p className="card-title">{description}</p>  
        </div>
      </div>
    </div>
  );
};

export default Eventos;